import {
  defaultBaseUrl
} from "./config.js";
import axios from 'axios'

import {
  notification
} from 'ant-design-vue'

// , , msg
function message(msg) {
  notification.open({
    message: "",
    description: ":" + msg
  })
}

async function axiosFetch(config) {
  let result

  await axios(config).then(res => {
    result = res.data
  })
  // 1, 
  if (result.code === config.code) {
    config.onSuccess(result)
  } else {
    message(result.message || result.msg)
    config.onFail(result)
  }
  return result
}

export default async ({
  // 
  url = "",
  // 
  data = {},
  // "GET" "POST" ...
  method = "GET",
  timeout = 99999,
  withCredentials = false,
  maxContentLength = 10000,
  responseType = "json",
  validateStatus = (status) => {
    return status >= 200 && status < 499; // 
  },
  onUploadProgress = () => {},
  onDownloadProgress = () => {},
  cancelToken,
  params,
  auth,
  baseUrl,
  headers = {},
  code = 1,
  onSuccess = function (res) {},
  onFail = function (res) {},
}) => {
  let _baseUrl = baseUrl || defaultBaseUrl
  // redirectUrl, url
  url = _baseUrl + url;
  // getdatapost
  if (method == "GET") {
    let dataStr = "";
    // data?&
    Object.keys(data).forEach(key => {
      dataStr += key + "=" + data[key] + "&";
    });

    if (dataStr !== "") {
      dataStr = dataStr.substr(0, dataStr.lastIndexOf("&"));
      url = url + "?" + dataStr;
    }
  }
  // config
  let requestConfig = {
    // `url`  URL
    url,
    // `method` 
    method: method, //  get
    // `headers` 
    headers,
    // `params`  URL 
    // (plain object) URLSearchParams 
    params,
    // `paramsSerializer`  `params` 
    // (e.g. https://www.npmjs.com/package/qs, http://api.jquery.com/jquery.param/)
    paramsSerializer: function (params) {
      return Qs.stringify(params, {
        arrayFormat: 'brackets'
      })
    },
    // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
    validateStatus,
    // `data` 
    //  'PUT', 'POST',  'PATCH'
    //  `transformRequest` 
    // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
    // - FormData, File, Blob
    // - Node  Stream
    data,
    // `timeout` (0 )
    //  `timeout` 
    timeout,
    // `withCredentials` 
    withCredentials, // 
    // `auth`  HTTP 
    //  `Authorization`  `headers`  `Authorization`
    auth,
    // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
    responseType, // 
    // `onUploadProgress` 
    onUploadProgress,
    // `onDownloadProgress` 
    onDownloadProgress,
    // `maxContentLength` 
    maxContentLength,
    // `cancelToken`  cancel token
    //  Cancellation 
    cancelToken,
    onSuccess,
    onFail,
    code,
    // , , 
    fasjfaslkjflaskjfaslkjflkasjflask: {

      // // `xsrfCookieName`  xsrf token cookie
      // xsrfCookieName: 'XSRF-TOKEN', // default
      // // `xsrfHeaderName`  xsrf token  HTTP 
      // xsrfHeaderName: 'X-XSRF-TOKEN', // 

      // `transformRequest` 
      //  'PUT', 'POST'  'PATCH' 
      //  ArrayBuffer Stream
      // transformRequest: [function (data) {
      //   //  data 
      //   return data;
      // }],

      // // `transformResponse`  then/catch 
      // transformResponse: [function (data) {
      //   //  data 

      //   return data;
      // }],


      // `adapter` 
      // //  promise  ( [response docs](#response-api)).
      // adapter: function (config) {
      //   /* ... */
      // },


      // // `httpAgent`  `httpsAgent`  node.js  http  https 
      // // `keepAlive` 
      // httpAgent: new http.Agent({
      //   keepAlive: true
      // }),
      // httpsAgent: new https.Agent({
      //   keepAlive: true
      // }), 

      // // `maxRedirects`  node.js  follow 
      // // 0 follow 
      // maxRedirects: 5, // 


      // // 'proxy' 
      // // `auth`  HTTP 
      // //  `Proxy-Authorization`  `header`  `Proxy-Authorization` 
      // proxy: {
      //   host: '127.0.0.1',
      //   port: 9000,
      //   auth: {
      //     username: 'mikeymike',
      //     password: 'rapunz3l'
      //   }
      // },
    }

  }

  try {
    // 
    // const response = await fetch(url, requestConfig);
    return await axiosFetch(requestConfig)
    // 
    // if (response.ok) {
    //   // json()
    //   const responseJson = await response.json();
    //   let obj = {
    //     url,
    //     responseJson
    //   }
    //   return responseJson;
    // } else {
    //   //,promise
    //   return new Promise(resolve => {
    //     resolve({
    //       code: 966,
    //       url,
    //       status: response.status,
    //       msg: ""
    //     })
    //   })
    // }
  } catch (error) {
    message('500')
    return new Promise(resolve => {
      resolve({
        code: 966,
        url,
        msg: error.message
      })
    })
  }
};