// permission.js
import router from "../router";
import store from "../store";
import {
  hasPermission
} from "@/assets/js/common";
import {
  getUserInfo
} from "@/router/cookie";

const whiteList = [
  // ,
  "/",
  "/login",
  "/exception/404",
  "/exception/403",
  "/exception/500"
];

router.beforeEach((to, from, next) => {
  if (store.state.account.userInfo && !store.state.account.userInfo.userId) {
    store.commit("account/SET_USER");
  }
  const isLogin = store.state.account.isLogin;
  if (isLogin) {
    // store
    if (store.state.router.roles.length === 0) {
      const role = getUserInfo().role;
      let roles = [];
      roles = [...roles, role];
      store.commit("router/SET_ROLES", roles);
      store.dispatch("router/GENERATE_ROUTES", {
        roles
      }).then(() => {
        // roles
        router.addRoutes(store.state.router.addRoutes); // 
        next({
          to,
          replace: true
        }); // hack addRoutes ,set the replace: true so the navigation will not leave a history record
      });
    } else {
      // 
      if (to.meta.roles && to.meta.roles.length) {
        // 
        if (hasPermission(store.state.router.roles, to.meta.roles)) {
          next();
        }
      } else {
        // 
        next();
      }
    }
    next();
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      // 
      next();
    } else {
      // 
      next("/login");
    }
  }
});