import fetch from "./fetch.js";
export const api = {
  // 1. /login
  loginV2: ({
      account,
      password
    }) =>
    fetch({
      url: "/app/user/loginV2",
      data: {
        account,
        password
      },
      method: "POST"
    }),
  // 2.  /home, /billProduct, /billRecord, /order, /userManager
  findSchoolList: () => fetch({
    url: "/app/school/findSchoolList",
    method: "GET"
  }),
  // 3. "" /home, /billRecord
  getBillConfigBy: ({
      orgNo,
      status
    }) =>
    fetch({
      url: "/app/bill/getBillConfigBy",
      data: {
        orgNo,
        status
      },
      method: "POST"
    }),
  // 4. () /home, /billProduct, /order
  getBillProductsByOrg: data => fetch({
    url: "/app/product/getBillProductsByOrg",
    data,
    method: "POST"
  }),
  // 5. "" /home
  updateBillConfig: data =>
    fetch({
      url: "/app/bill/updateBillConfig",
      data,
      method: "POST"
    }),
  // 6. "" /home
  createBillConfig: data => fetch({
    url: "/app/bill/createBillConfig",
    data,
    method: "POST"
  }),
  // 7. () /billProduct
  createBillProduct: ({
      senderId,
      method,
      productName,
      price,
      discountPrice,
      desc,
      orgNo
    }) =>
    fetch({
      url: "/app/product/createBillProduct",
      data: {
        senderId,
        method,
        productName,
        price,
        discountPrice,
        desc,
        orgNo
      },
      method: "POST"
    }),

  // 8.  /billRecord, /order, /userManager
  getSchoolDeparts: ({
      schoolCode
    }) =>
    fetch({
      url: "/app/school/getSchoolDeparts",
      data: {
        schoolCode
      },
      method: "GET"
    }),
  // 9.  /billRecord
  getBillsBy: ({
      orgNo,
      billId,
      depart,
      pageSize,
      pageNum
    }) =>
    fetch({
      url: "/app/bill/getBillsBy",
      data: {
        orgNo,
        billId,
        depart,
        pageSize,
        pageNum
      },
      method: "POST"
    }),
  // 10.  /order
  getOrders: ({
      orgNo,
      depart,
      productId,
      pageSize,
      pageNum,
      status
    }) =>
    fetch({
      url: "/app/order/getOrders",
      data: {
        orgNo,
        depart,
        productId,
        pageSize,
        pageNum,
        status
      },
      method: "POST"
    }),
  // 11.  /selectOrder, /userManager
  findUser: ({
    account
  }) => fetch({
    url: "/app/user/findUser",
    data: {
      account
    },
    method: "POST"
  }),
  // 12.  /selectOrder
  getUserOrders: ({
      buyerId,
      pageSize,
      pageNum
    }) =>
    fetch({
      url: "/app/order/getUserOrders",
      data: {
        buyerId,
        pageSize,
        pageNum
      },
      method: "POST"
    }),
  // 13.   /userManager
  getUsers: ({
      pageSize,
      orgNo,
      pageNum,
      role
    }) =>
    fetch({
      url: "/app/user/getUsers",
      data: {
        pageSize,
        orgNo,
        pageNum,
        role
      },
      method: "POST"
    }),
  // 14.   /userManager
  updateUserInfo: ({
      userId,
      orgNo,
      phone,
      role
    }) =>
    fetch({
      url: "/app/user/updateUserInfo",
      data: {
        userId,
        orgNo,
        phone,
        role
      },
      method: "POST"
    }),
  // 15.   /userManager
  getTeacherClazzList: ({
      userId
    }) =>
    fetch({
      url: "/app/user/getTeacherClazzList",
      data: {
        userId
      },
      method: "POST"
    }),
  // 16.   /userManager
  addClassRelationV3: ({
      userId,
      classList
    }) =>
    fetch({
      url: "/app/user/addClassRelationV3",
      data: {
        userId,
        classList
      },
      method: "POST"
    }),
  // 17.   /userManager
  deleteClassRelationV2: ({
      userId,
      classList
    }) =>
    fetch({
      url: "/app/user/deleteClassRelationV2",
      data: {
        userId,
        classList
      },
      method: "POST"
    }),
  // 18.   /inform
  sendInform: ({
      title,
      content,
      clazz,
      senderId
    }) =>
    fetch({
      url: "/app/notification/sendInform",
      data: {
        title,
        content,
        clazz,
        senderId
      },
      method: "POST"
    }),
  // 19.   /inform
  getInform: ({
      clazz,
      pageSize,
      pageNum
    }) =>
    fetch({
      url: "/app/getUserNotifications",
      data: {
        clazz,
        pageSize,
        pageNum
      },
      method: "POST"
    }),
  // 20. Token  /mall, /banner, /homework, /news
  getUploadToken: () => fetch({
    url: "/app/tool/getUploadToken",
    method: "POST"
  }),
  // 21.   /mall
  createProduct: ({
      senderId,
      method,
      productName,
      pics,
      price,
      discountPrice,
      desc
    }) =>
    fetch({
      url: "/app/product/create",
      data: {
        senderId,
        method,
        productName,
        pics,
        price,
        discountPrice,
        desc
      },
      method: "POST"
    }),
  // 22.   /mall
  getUserProducts: ({
      pageSize,
      pageNum
    }) =>
    fetch({
      url: "/app/product/getUserProducts",
      data: {
        pageSize,
        pageNum
      },
      method: "POST"
    }),
  // 23. banner  /banner
  getHomeBanner: () => fetch({
    url: "/app/common/getHomeBanner",
    method: "POST"
  }),
  // 24.   /homework
  createHomework: data =>
    fetch({
      url: "/app/homework/create",
      data,
      method: "POST"
    }),
  // 25.   /homework
  getUserHomeworks: data =>
    fetch({
      url: "/app/homework/getUserHomeworks",
      data: data,
      method: "POST"
    }),
  // 26.   /news
  getNews: ({
      pageSize,
      pageNum
    }) =>
    fetch({
      url: "/app/news/getNews",
      data: {
        pageSize,
        pageNum
      },
      method: "POST"
    }),
  // 27.  /mall
  imgUpload: data =>
    fetch({
      data: data,
      method: "POST",
      baseUrl: "https://upload.qiniup.com",
      header: {
        "Content-Type": "multipart/form-data"
      }
    }),
  getClassByUserId: data => fetch({
    data,
    code: 0,
    method: "POST",
    baseUrl: "https://duchengedu.com/wechatHyx/forLeave/getClassByUserId"
  }),
  getUserNotifications: data => fetch({
    data,
    method: "POST",
    url: "/app/notification/getUserNotifications"
  }),
  syncSchoolData: data => fetch({
    url: `/syncStu/syncStuBySchoolCode?schoolCode=${data.schoolCode}`,
    data: {},
    method: "POST",
    code: 0
  }),
};